import { Canvas, useFrame } from "@react-three/fiber";
import { useLoader } from "@react-three/fiber";
import { Environment, Html, OrbitControls, useGLTF } from "@react-three/drei";
import { Preload } from '@react-three/drei'
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import { Suspense } from "react";
import { Glitch, EffectComposer } from "@react-three/postprocessing";



export default class Choices extends Component {
    constructor(props) {
        super(props);

        var dataSet = [
            {
                question: "Q1",
                answers: [
                    "https://t4.ftcdn.net/jpg/02/07/87/79/360_F_207877921_BtG6ZKAVvtLyc5GWpBNEIlIxsffTtWkv.jpg",
                    "https://t4.ftcdn.net/jpg/02/07/87/79/360_F_207877921_BtG6ZKAVvtLyc5GWpBNEIlIxsffTtWkv.jpg",
                ],
            },
            {
                question: "Q2",
                answers: ["dress/scene.gltf", "floral_dress/scene3.gltf"],
                isModel: true,
                env: "env1.hdr",
                scales: [100, 3]
            },
            {
                question: "Q3",
                answers: [
                    "https://t4.ftcdn.net/jpg/02/07/87/79/360_F_207877921_BtG6ZKAVvtLyc5GWpBNEIlIxsffTtWkv.jpg",
                    "https://t4.ftcdn.net/jpg/02/07/87/79/360_F_207877921_BtG6ZKAVvtLyc5GWpBNEIlIxsffTtWkv.jpg",
                ],
            },
            {
                question: "Q4",
                answers: ["car/scene.gltf", "floral_dress/scene3.gltf"],
                isModel: true,
                env: "/env2.hdr",
                scales: [1, 3]
            },
            {
                question: "Q5",
                answers: [
                    "https://t4.ftcdn.net/jpg/02/07/87/79/360_F_207877921_BtG6ZKAVvtLyc5GWpBNEIlIxsffTtWkv.jpg",
                    "https://t4.ftcdn.net/jpg/02/07/87/79/360_F_207877921_BtG6ZKAVvtLyc5GWpBNEIlIxsffTtWkv.jpg",
                ],
            },
            {
                question: "Q6",
                answers: [
                    "https://t4.ftcdn.net/jpg/02/07/87/79/360_F_207877921_BtG6ZKAVvtLyc5GWpBNEIlIxsffTtWkv.jpg",
                    "https://t4.ftcdn.net/jpg/02/07/87/79/360_F_207877921_BtG6ZKAVvtLyc5GWpBNEIlIxsffTtWkv.jpg",
                ],
            },
        ];

        this.state = {
            current: 0,
            dataSet: dataSet,
            choices: [],
            finished: false,
            final_result: 0,
            lastClick: 0,
        };
        this.handleClick = this.handleClick.bind(this);
    } // end constructor

    handleClick(choice) {
        var time = Date.now();
        if (time - this.state.lastClick < 10) {
            console.log("catch");
            return;
        }
        if (this.state.current === this.state.dataSet.length - 1) {
            this.setState({ finished: true, choices: this.state.choices + [choice], lastClick: time });
            console.log(this.state.choices);
        } else {
            this.setState({ current: this.state.current + 1, choices: this.state.choices + [choice], lastClick: time });
        }
    }

    componentDidMount() { }

    render() {
        var icon = (
            <img
                class="icon"
                src="https://t4.ftcdn.net/jpg/02/07/87/79/360_F_207877921_BtG6ZKAVvtLyc5GWpBNEIlIxsffTtWkv.jpg"
                height="80px"
                style={{ marginTop: "15px", marginLeft: "15px", "zIndex": "100" }}
                onClick={() => window.location.reload(false)}
                alt
            ></img>
        );

        if (this.state.finished) {
        const Model = () => {
                const myMesh = React.useRef();
                useFrame(({ clock }) => { });
                const gltf = useLoader(GLTFLoader, "b_room/scene.gltf");
                return (
                    <mesh ref={myMesh} position={new THREE.Vector3(-10, 0, 0)}>
                        <primitive object={gltf.scene} scale={4.5}></primitive>
                    </mesh>
                );
            };
            return (
                <div>
                    {icon}
                    <div>{this.state.choices}</div>
                    <div className="App res">
                        <Canvas camera={{ position: [-40, 20, 20] }}>
                            <Suspense fallback={null}>
                                <Model></Model>
                                <spotLight position={[1, 1, 1]} angle={0.15} penumbra={1} />
                        <spotLight position={[-1, -1, -1]} angle={0.15} penumbra={1} />
                        <pointLight position={[0, 5, 0]} />
                                <OrbitControls></OrbitControls>
                                <Preload all />
                            </Suspense>
                        </Canvas>
                    </div>
                </div>
            );
        }
        return (
            <div>
                {icon}
                <QuizArea
                    handleClick={this.handleClick}
                    dataSet={this.state.dataSet[this.state.current]}
                    models={this.state.models}
                />
            </div>
        );
    }
}

function Question(props) {
    var style = { marginLeft: "100px" };
    return <h1 style={style}>{props.dataSet.question}</h1>;
}

function Answer(props) {
    return (
        <img
            class="choice"
            src={props.image}
            onClick={() => props.handleClick(props.choice)}
            style={{ marginLeft: "100px", cursor: "pointer" }}
        ></img>
    );
}

function AnswerList(props) {
    var answers = [];
    for (let i = 0; i < props.dataSet.answers.length; i++) {
        answers.push(
            <Answer
                choice={i}
                handleClick={props.handleClick}
                image={props.dataSet.answers[i]}
            />
        );
    }

    var style = {};
    return <div style={style}>{answers}</div>;
}

function QuizArea(props) {
    if (props.dataSet.isModel === true) {
        return (
            <div class="modelQuestion">

                <div className="App">
                    <Canvas
                        style={{ marginTop: "-200px" }}
                        camera={{ position: [0, 100, 500] }}
                    >
                        <ambientLight intensity={0.5} />
                        <spotLight position={[10, 10, 10]} angle={0.15} penumbra={1} />
                        <spotLight position={[-10, -10, -10]} angle={0.15} penumbra={1} />
                        <pointLight position={[-10, -10, -10]} />
                        <Html><div style={{ marginTop: "-150px" }}><h1>{props.dataSet.question}</h1></div></Html>
                        <Suspense fallback={null}>
                            <ModelAnswerList
                                dataSet={props.dataSet}
                                handleClick={props.handleClick}
                                models={props.models}
                            />
                            <Preload all />

                        </Suspense>

                    </Canvas>
                </div>
            </div>
        );
    }

    return (
        <div >
            <Question dataSet={props.dataSet} />

            <AnswerList dataSet={props.dataSet} handleClick={props.handleClick} />
        </div>
    );
}

function ModelAnswerList(props) {
    const Model = (choice) => {
        const [hovered, setHovered] = useState(false)
        const myMesh = React.useRef();
        useFrame(({ clock }) => {
            const a = clock.getElapsedTime();
            myMesh.current.rotation.y = a / 3;
        });
        const gltf = useLoader(GLTFLoader, props.dataSet.answers[choice]);
        const shift = choice === 0 ? -250 : 250;
        return (
            <mesh
                ref={myMesh}
                onClick={() => { props.handleClick(choice) }}
                onPointerOver={() => setHovered(true)}
                onPointerOut={() => setHovered(false)}
                position={new THREE.Vector3(shift, 0, 0)}
                scale={hovered ? props.dataSet.scales[choice] * 1.1 : props.dataSet.scales[choice]}
            >
                <primitive object={gltf.scene}></primitive>
            </mesh>
        );
    };
    const Environment0 = () => {
        return <Environment files={props.dataSet.env}></Environment>;
    };
    return (
        <>
            {Model(0)}
            {Model(1)}

        </>
    );
}

